module.exports = {"id":6,"slug":"e40","title":"О Е40","teaser":"Официально: как выглядит проект по&nbsp;восстановлению водного пути Е40 и&nbsp;что думают по&nbsp;этому поводу государственные организации Беларуси.","lead":"На&nbsp;этой странице собраны документы по&nbsp;проекту водного пути Е40, которые есть в&nbsp;открытом доступе&nbsp;&#151; брошюры, технико-экономическое обоснование и&nbsp;экологическая оценка проекта. Также здесь можно найти 17&nbsp;ответов различных государственных учреждений Беларуси на&nbsp;официальные запросы ОО&nbsp;&laquo;Багна&raquo; о&nbsp;позиции по&nbsp;проекту обустройства этой трассы.","cover":"","date":"","sections":[{"title":"Предоставленные документы по&nbsp;проекту восстановления водного пути Е40","isActive":false,"paragraphs":[{"list":[{"text":"Брошюра о&nbsp;проекте","link":"https://drive.google.com/drive/folders/0B1Oup9NEQTK3YndKdGs4d2JONWs?usp=sharing"},{"text":"Рекомендации экспертов","link":"https://drive.google.com/open?id=0B1Oup9NEQTK3Ym52c196M0lWMnM"},{"text":"Экологическая оценка","link":"https://drive.google.com/open?id=0B1Oup9NEQTK3R2ZMdjctbzlQYjg"},{"text":"05.2015, Первый промежуточный отчёт","link":"https://drive.google.com/open?id=0B1Oup9NEQTK3WFU5eGF3RHRheU0"},{"text":"06.2016, ООН Протокол заседания Экологического совета","link":"https://drive.google.com/open?id=0B1Oup9NEQTK3Y3dyUkhFQVpDSk0"},{"text":"08.2015, Второй промежуточный отчёт","link":"https://drive.google.com/open?id=0B1Oup9NEQTK3cFJBMTlJT0VhcU0"},{"text":"30.06.2015, Comments by&nbsp;DNEPROBUG on&nbsp;the 1st IntR_En","link":"https://drive.google.com/open?id=0B1Oup9NEQTK3aXpOUzNacS04M0U"},{"text":"Описание проекта","link":"https://drive.google.com/open?id=0B1Oup9NEQTK3TXN0dmdUaGxhUVU"}]}]},{"title":"Позиция госорганизаций о&nbsp;проекте восстановления магистрального водного пути Е40","isActive":false,"paragraphs":[{"text":"Письмо с уведомлением о возможной реализации проекта Е40 было разослано в ряд государственных учреждений. Академия наук, Центральный научно-исследовательский институт комплексного использования водных ресурсов, Полесский государственный радиационно-экологический заповедник и Припятский национальный парк  высказали свою негативную позицию."},{"list":[{"text":"Письменные ответы от&nbsp;государственных организаций","link":"https://drive.google.com/open?id=0B1Oup9NEQTK3TFExckZOdTUzZjQ"}]}]}]}

