module.exports = {"id":2,"slug":"expertise","title":"Экспертиза","teaser":"","lead":"","cover":"","date":"","sections":[{"title":"Независимая биологическая, экономическая и&nbsp;гидрологическая оценка проекта восстановления водного пути Е40","isActive":false,"paragraphs":[{"list":[{"text":"Биологическая часть. Оринитологический отчет научных сотрудников лаборатории орнитологии Научно-практического центра Национальной академии наук Беларуси по&nbsp;биоресурсам. Пинчук и&nbsp;Карлионова, 2017","link":"https://drive.google.com/file/d/0B1Oup9NEQTK3N192VjJQcU9WeDA/view?usp=sharing"},{"text":"Биологическая часть. Оценка влияния на&nbsp;природные комплексы Полесья и&nbsp;биоразнообразие региона. НПЦ по&nbsp;биоресурсам НАН Беларуси","link":"https://drive.google.com/file/d/0B1Oup9NEQTK3aW5aNS0xTy1XNkE/view?usp=sharing"},{"text":"Гидрологическая часть. Комментарий кандидата технических наук доцент Брестского государственного технического университета, гидролог Николай Шешко","link":"https://drive.google.com/file/d/0B1Oup9NEQTK3eEFvaWpwWHJqWUk/view?usp=sharing"},{"text":"Гидрологическая часть. Комментарий гидролога Dr&nbsp;Janusz Żelaziński, Варшавский университет","link":"https://drive.google.com/file/d/0B1Oup9NEQTK3MlZfemlGS2d1QUk/view?usp=sharing"},{"text":"Экономическая часть. Об&nbsp;инвестиционных рисках проекта по&nbsp;обустройству водного пути Е40. Бизнес союз предпринимателей и&nbsp;нанимателей имени профессора М.&nbsp;С.&nbsp;Кунявского (БСПН)","link":"https://drive.google.com/file/d/0B1Oup9NEQTK3amotVXZ1TTlIUjA/view?usp=sharing"},{"text":"Экономическая часть. Оценка документа &laquo;Restoration of&nbsp;Inland Waterway E40 Dnieper-Vistula: from Strategy to&nbsp;Planning. Final Feasibility Study Report&raquo; (технико-экономическое обоснование проекта). Варшавский университет, экономист С.&nbsp;Волосюк и&nbsp;профессор М.Чайковский","link":"https://drive.google.com/file/d/0B1Oup9NEQTK3NTNOQVMyYzItem8/view?usp=sharing"},{"text":"Карта-схема необходимых работ на&nbsp;реке Припять","link":"https://drive.google.com/file/d/0B1Oup9NEQTK3ZmlKSVQ5SzI4U1k/view?usp=sharing"}]}]}]}

