module.exports = {"sections":[{"items":[{"author":"","cover":"","date":"2017-07-20","link":"http://www.ptushki.org/info/press/item/20196.html","publicationTitle":"The E40 Waterway: no shipping and no Polesye?","teaser":"The public campaign \"Stop E40! We will keep the rivers of Belarus in a natural state\" was officially launched on July 19, 2017 in Minsk. On this occasion s press conference took place, where speakers described the main ecological and economic consequences of the construction of a new waterway that will pass 500 km of our Polesye.","title":"NGO APB BirdLife Belarus"},{"author":"","cover":"","date":"2017-07-20","link":"http://bahna.land/2017/07/20/e40-korabli-nie-poplyvut/","publicationTitle":"The E40 Waterway: no shipping and no Polesye?","teaser":"The public campaign \"Stop E40! We will keep the rivers of Belarus in a natural state\" was officially launched on July 19, 2017 in Minsk. On this occasion s press conference took place, where speakers described the main ecological and economic consequences of the construction of a new waterway that will pass 500 km of our Polesye.","title":"NGO Bahna"},{"author":"Andrei Borodin","cover":"https://wildlife.by/bitrix/templates/IBWM/img/logo.png","date":"2017-07-21","link":"https://wildlife.by/ecology/news/ofitsialno-startovala-obshchestvennaya-kampaniya-stop-e40-/","publicationTitle":"The public campaign \"Stop E-40!\" officially started","teaser":"On July 19, a press conference was held on the official launch of the public campaign \"Stop E-40! We will keep the rivers of Belarus in a natural state \". This abbreviation stands for not a harmful food additive, as you might think, but the construction project of a waterway that should connect the Baltic Sea with the Black Sea.","title":"Wild nature"},{"author":"Elena Khramtsova","cover":"http://homel.greenbelarus.info/sites/all/themes/green/img/logo.png","date":"2017-07-20","link":"http://homel.greenbelarus.info/articles/20-07-2017/unichtozhit-prirodu-polesya-za-150-mln-evro-ekologicheskie-organizacii-vystupili","publicationTitle":"Polesye nature destruction for 150 million euros: environmental organizations opposed the E-40 construction","teaser":"Press conference of Belarusian environmental organizations, timed to the launch of the public campaign \"Stop E-40! We will keep the rivers of Belarus in a natural state\", resulted in an ardent discussion with supporters and experts of the project. Both sides noted that for full argumentation they lack the Environmental Impact Assessment, which the NGOs hope will be held before the implementation of the work.","title":"Green Network partnership"}],"title":"Environmental NGOs"},{"items":[{"author":"Vіktar Malyshchyts","cover":"https://img.tyt.by/i/by5/news/logo@2x.png","date":"2017-07-25","link":"https://news.tut.by/culture/552826.html","publicationTitle":"Flooding, fog and a starry sky. The beauty of Belarusian Polesye in photos","teaser":"Environmental experts are debating on the reconstruction of the E40 route, which connected the Baltic Sea to the Black. Astronomer Victor Malyschits is sure that if the cascade of dams is built on the entire Pripyat, it will lead to irreversible changes in the nature.","title":"TUT.BY"}],"title":"Mass Media"},{"items":[{"author":"","cover":"","date":"","link":"","publicationTitle":"","teaser":"","title":""}],"title":"Reprints"}],"title":"The public campaign publications <br>on other websites and in the media"}

